import React, { Component } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';
import Swal from 'sweetalert2';

class Forms extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      vehicleNumber:'',
      vehicleCapacity:'',
      vehicleDeliRate:'',
      lorries:[],
      btnText:'Save',btnColor:'dark'
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount(){
    this.getLorries();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }

 onSubmit(e){
    e.preventDefault();
    if(!this.state.btnText.localeCompare('Save')){
      this.addLorry();
    }
    else{
      this.updateLorry();
    }
    
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }

  //adding a new lorry to database
  addLorry= async()=>{
    await axios.post('/addlorry',{
      vehicleNumber : this.state.vehicleNumber,
      vehicleCapacity : this.state.vehicleCapacity,
      vehicleDeliRate:this.state.vehicleDeliRate
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('SuccessfullyAdded')){
        Swal.fire(
          'Successful!',
          '',
          'success'
        )
      }
      else{
        Swal.fire(
          'Failed!',
          '',
          'error'
        )
      }
    })
    this.getLorries();
  }
  //updating lorry table in database
  updateLorry= async()=>{
    try{
      await axios.post('/updatelorry',{
        vehicleNumber : this.state.vehicleNumber,
        vehicleCapacity : this.state.vehicleCapacity,
        vehicleDeliRate:this.state.vehicleDeliRate
      }).then(res =>{
        var status=res.data.status;
        if(!status.localeCompare('SuccessfullyAdded')){
          Swal.fire(
            'Successful!',
            '',
            'success'
          )
        }
        else{
          Swal.fire(
            'Failed !',
            '',
            'error'
          )
        }
      })
      this.getLorries();
    }catch(e){
      Swal.fire(
        'Failed !',
        '',
        'error'
      )
    }

  }

  //fetch lorries from database
  getLorries(){
    fetch('/getlorries')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        lorries : json
      });
    });
  }
//event handlers for lorry updates and deletes
  onRowSelect=(rowKeys)=>{
    this.setState(
      {
        vehicleNumber:rowKeys.vehicleNumber,
        vehicleCapacity : rowKeys.vehicleCapacity,
        vehicleDeliRate :rowKeys.vehicleDeliRate,
        btnText:'Update',
        btnColor:'info'
      }
    )
  }

 onAfterDeleteRow=(rowKeys)=> {
    axios.post('/deletelorry',{
      vehicleNumber:rowKeys
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('SuccessfullyAdded')){
        Swal.fire(
          'Successful!',
          '',
          'success'
        )
        this.getLorries();
      }
      else{
        Swal.fire(
          'Failed!',
          '',
          'error'
        )
      }
    })
    
  }
  
  options = {
    afterDeleteRow: this.onAfterDeleteRow  // A hook for after droping rows.
  };
  
  selectRowProp = {
    mode: 'radio',
    onSelect: this.onRowSelect,
    bgColor:'yellow'
  };

  //event handler for clear button
  clearForm=()=>{
    this.setState(
      {
        vehicleNumber:'',
        vehicleCapacity : '',
        vehicleDeliRate : '',
        btnText:'Save',
        btnColor:'dark'
      })
  }
  render() {
    return (
      <div className="animated fadeIn">
         <Container>
        <Row>
          <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong>Lorries</strong>
              </CardHeader>
              <CardBody>
                <Form onSubmit={this.onSubmit}>
              <FormGroup row>
                  </FormGroup>
                <FormGroup>
                  <Label htmlFor="lorry">Lorry Number</Label>
                  <Input type="text" name="vehicleNumber"   value={this.state.vehicleNumber} onChange={this.onChange} />
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="capacity">Capacity</Label>
                  <Input type="text" name="vehicleCapacity" value={this.state.vehicleCapacity} onChange={this.onChange}/>
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="deliveryRate">Payment Rate<br/><smal>(per km)</smal></Label>
                  <Input type="text" name="vehicleDeliRate" value={this.state.vehicleDeliRate} onChange={this.onChange}/>
                </FormGroup>
                  <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Button block color={this.state.btnColor}>{this.state.btnText}</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={this.clearForm}>clear</Button>
                    </FormGroup>
                  </Col>
                  </FormGroup>
                  </Form>
              </CardBody>
            </Card>
          </Col>
          <Col  xs="12" sm="6">
          <Card>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
          <div>
            <BootstrapTable data={this.state.lorries} height='330' scrollTop={'Bottom'} deleteRow={ true } selectRow={this.selectRowProp} options={this.options}
            search={true} 
            tableStyle={ { border: '#0000FF 2.5px solid' } }
            containerStyle={ { border: '#FFBB73 2.5px solid' } }
            headerStyle={ { border: 'red 1px solid' } }
            bodyStyle={ { border: 'green 1px solid' }}>
              <TableHeaderColumn width="120" dataField="vehicleNumber" isKey>Lorry Number</TableHeaderColumn>
              <TableHeaderColumn width="110" dataField="vehicleCapacity">Capacity</TableHeaderColumn>
              <TableHeaderColumn width="140" dataField="DATE_FORMAT(vehicleRegDate,'%Y-%m-%d')">Date Registered</TableHeaderColumn>
              <TableHeaderColumn width="110" dataField="vehicleDeliRate">Rate</TableHeaderColumn>
            </BootstrapTable>
            </div>
            </CardBody>
            </Card>
    
          </Col>
        
        </Row>  
        </Container>
       
        
      </div>
    );
  }
}

export default Forms;
